import React from 'react'
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import SubtractIcon from '@material-ui/icons/Remove';


export const AddButton = (props) => {
    const onClick = props.onClick;
    const disabled = props.disabled;
    return (
        <Fab onClick={onClick} style={{ marginRight: 10 }} disabled={disabled}>
            <AddIcon />
        </Fab>
    )
}

export const SubtractButton = (props) => {
    const onClick = props.onClick;
    const disabled = props.disabled;
    return (
        <Fab onClick={onClick} disabled={disabled}>
            <SubtractIcon />
        </Fab>
    )
}

export const ResetButton = () => {
    return (
        <Fab style={{backgroundColor: '#ff6161', color: 'white', width: 150, fontSize:18}} variant="extended" onClick={() => {window.location="/card-website"}} >
            <strong>Reset</strong>
        </Fab>
    )
}