import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { NumberOfCardsDropDown, SuitDropDown, CardValueDropDown } from './dropdown';
import Tracker from './tracker';
import { NameInput } from './textInput';

export default (props) => {
    var numRows = props.numRows;
    const [r1, setr1] = React.useState(0);
    const [r2, setr2] = React.useState(0);
    const [r3, setr3] = React.useState(0);
    const [r4, setr4] = React.useState(0);
    const [r5, setr5] = React.useState(0);
    const [r6, setr6] = React.useState(0);

    return (
        <Table>
            <TableHead>
                <TableRow>
                    <TableCell style={{fontSize:18}}><b>Set #</b></TableCell>
                    <TableCell style={{fontSize:18}}><b>Suit</b></TableCell>
                    <TableCell style={{fontSize:18}}><b>Value</b></TableCell>
                    <TableCell style={{fontSize:18}}><b>Times Played</b></TableCell>
                    <TableCell style={{fontSize:18}}><b>Name</b></TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr1} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r1} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr2} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r2} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr3} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r3} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr4} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r4} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr5} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r5} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell component="th" scope="row"> <NumberOfCardsDropDown setrow={setr6} /> </TableCell>
                    <TableCell><SuitDropDown /></TableCell>
                    <TableCell><CardValueDropDown /></TableCell>
                    <TableCell><Tracker max={r6} /></TableCell>
                    <TableCell><NameInput /></TableCell>
                </TableRow>
            </TableBody>
        </Table>
    )
}