import {AddButton, SubtractButton} from './button';
import React from 'react';

export default (props) => {
    const max = props.max;
    const [numberOfCardsPlayed, setNumberOfCardsPlayed] = React.useState(0);

    const handleAdd = () => {
        console.log("add clicked");
        setNumberOfCardsPlayed(numberOfCardsPlayed+1);
    }
    const handleSubtract = () => {
        console.log("subtract has been clicked");
        setNumberOfCardsPlayed(numberOfCardsPlayed-1);
    }

    return (
        <div style={{display:'flex', flexDirection:'row'}}>
            <div style={{marginRight: 20, display:'flex', alignItems:'center',justifyContent:'center', fontSize: 30, border: 'solid', borderWidth: 1, borderColor:'#c4c4c4', width:56, height:54, borderRadius:4}}>
               {numberOfCardsPlayed}
            </div>
            <AddButton onClick={handleAdd} disabled={numberOfCardsPlayed+1>max}/>
            <SubtractButton onClick={handleSubtract} disabled={numberOfCardsPlayed-1 < 0}/>
        </div>
    )
}