import React from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

export const NumberOfCardsDropDown = (props) => {
    const [numberOfCards, setNumberOfCards] = React.useState('');
    const setRow = props.setrow;

    const handleChange = event => {
        setNumberOfCards(event.target.value);
        // console.log(event.target.value);
        setRow(event.target.value);
    };

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={numberOfCards}
                onChange={handleChange}
                fullWidth
                style={{fontSize: 30, padding: 0, margin: 0}}
            >
                <MenuItem value={0}><em>None</em></MenuItem>
                <MenuItem value={1}>1</MenuItem>
                <MenuItem value={2}>2</MenuItem>
                <MenuItem value={3}>3</MenuItem>
                <MenuItem value={4}>4</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={6}>6</MenuItem>
            </Select>
        </FormControl>
    )
}

export const SuitDropDown = () => {
    const [suit, setSuit] = React.useState('');

    const handleChange = event => {
        setSuit(event.target.value);
    };

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={suit}
                onChange={handleChange}
                fullWidth
            >
                <MenuItem value=""><em>None</em></MenuItem>
                <MenuItem value={1} ><Spade /></MenuItem>
                <MenuItem value={2} ><Heart /></MenuItem>
                <MenuItem value={3} ><Club /></MenuItem>
                <MenuItem value={4} ><Diamond /></MenuItem>
            </Select>
        </FormControl>
    )
}

const Spade = () => (<div style={{fontSize:30}}>♠</div>)
const Heart = () => (<div style={{color:'red', fontSize:30}}>♥</div>)
const Club = () => (<div style={{fontSize:30}}>♣</div>)
const Diamond = () => (<div style={{color:'red', fontSize:30}}>♦</div>)

export const CardValueDropDown = () => {
    const [cardValue, setCardValue] = React.useState('');

    const handleChange = event => {
        setCardValue(event.target.value);
    };

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={cardValue}
                onChange={handleChange}
                fullWidth
            >
                <MenuItem value=""><em>None</em></MenuItem>
                <MenuItem value={1}>A</MenuItem>
                <MenuItem value={13}>K</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={12}>Q</MenuItem>
                <MenuItem value={11}>J</MenuItem>             
                <MenuItem value={2}>2</MenuItem>
                <MenuItem value={3}>3</MenuItem>
                <MenuItem value={4}>4</MenuItem>
                <MenuItem value={6}>6</MenuItem>
                <MenuItem value={7}>7</MenuItem>
                <MenuItem value={8}>8</MenuItem>
                <MenuItem value={9}>9</MenuItem>

            </Select>
        </FormControl>
    )
}

